.wrap_movies {
  display: flex;
  max-width: 1250px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.movie {
  width: 48%;
  margin: 50px 1%;
  padding: 50px 30px 40px;
  background-color: #fff;
  box-shadow: 5px 5px 30px rgba(0,0,0,0.1);
  box-sizing: border-box;
}

.movie__img {
  float: left;
  width: 32%;
  margin-top: -70px;
  box-shadow: 2px 2px 15px rgba(0,0,0,0.2);
}

.movie__data {
  float: left;
  width: 68%;
  padding-left:30px;
  box-sizing: border-box;
}
.movie__title {
  margin:0 0 20px;
  padding:0;
  font-size: 24px;
  color: #111;
}
.movie__year {
  font-size: 14px;
  color: #aaa;
}

.genres {
  margin: 0;
  padding: 0;
}
.genres .genres__genre {
  display: inline-block;
  margin-right: 8px;
  font-size: 14px;
  color: #aaa;
  list-style: none;
}

.movie__summary {
  margin: 10px 0;
  font-size: 14px;
  color: #777;
  line-height: 1.4;
}